/* Global Variables */
:root {
  --main-color: #3E5879;
  --second-color: #213555;
  --third-color: #607597;
  --widget-bg: #eaeaea;
  --text-color: #000000;
  --border-radius: 5px;
  --hover-effect: #eaeaea;
  --text-muted: #cfcfcf;
}

/* Theme Configuration */
[data-theme="watermelon"] {
  --primary: var(--main-color);
  --dt-primary-color: var(--main-color);
  --blue-avatar-bg: var(--main-color);
  --blue-avatar-color: var(--main-color);
  --blue: var(--main-color);
}

/* Global Styles */
[data-theme="watermelon"] body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  background: linear-gradient(-135deg, #d1dcef, #63799d);
  color: var(--text-color);
  line-height: 1.7;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

[data-theme="watermelon"] a {
  color: #666;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease, transform 0.3s;
}

[data-theme="watermelon"] a:hover {
  color: var(--second-color);
  transform: scale(1.05);
}

[data-theme="watermelon"] .small, small {
  font-size: 95%;
}

[data-theme="watermelon"] .h6.uppercase, h6.uppercase {
  font-weight: 600;
  font-size: 11.5px;
}

/* Layout Styles */
[data-theme="watermelon"] .page-container,
[data-theme="watermelon"] .layout-main-section {
  background-color: var(--widget-bg);
  padding: 30px;
  border-radius: var(--border-radius);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  margin: 15px;
  scrollbar-color: #000000 #EDE9E4;
}

/* Typography */
[data-theme="watermelon"] h1,
[data-theme="watermelon"] h2,
[data-theme="watermelon"] h3,
[data-theme="watermelon"] h4,
[data-theme="watermelon"] h5,
[data-theme="watermelon"] h6 {
  color: var(--text-color);
  font-weight: bold;
  letter-spacing: 0.5px;
}

/* Navigation Bar */
[data-theme="watermelon"] .navbar {
  background: radial-gradient(var(--main-color)50%, var(--second-color));
  padding: 15px 20px;
  border-radius: var(--border-radius);
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.2);
}

[data-theme="watermelon"] .navbar-nav>li>a {
  color: #fff !important;
  padding: 12px 18px;
  font-size: 1rem;
  border-radius: var(--border-radius);
  transition: background-color 0.3s, transform 0.3s;
}

[data-theme="watermelon"] .navbar-nav>li>a:hover {
  background-color: var(--hover-effect);
  transform: scale(1.05);
}

[data-theme="watermelon"] .navbar-brand:hover {
  color: #ddd !important;
}

/* Sidebar Styles */
[data-theme="watermelon"] .standard-sidebar-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-radius: var(--border-radius);
  transition: background-color 0.3s, color 0.3s;
}

[data-theme="watermelon"] .standard-sidebar-item.selected {
  background: linear-gradient(to left, var(--main-color), var(--second-color));
  color: #fff;
}

[data-theme="watermelon"] .standard-sidebar-item.selected a,
[data-theme="watermelon"] .standard-sidebar-item.selected a svg {
  color: #fff;
  fill: #fff;
}

[data-theme="watermelon"] .standard-sidebar-item:hover {
  background-color: var(--second-color);
  color: #fff;
}

[data-theme="watermelon"] .standard-sidebar-item:hover a,
[data-theme="watermelon"] .standard-sidebar-item:hover svg {
  color: #fff;
  fill: #fff;
}

/* Widget Styles */
[data-theme="watermelon"] .widget {
  background-color: var(--widget-bg);
  border: 1px solid var(--second-color);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  border-radius: var(--border-radius);
  padding: 25px;
  margin-bottom: 25px;
  transition: transform 0.3s ease-in-out;
}

[data-theme="watermelon"] .widget:hover {
  transform: translateY(-3px);
}

[data-theme="watermelon"] .widget.shortcut-widget-box .widget-head .widget-title i {
  color: var(--text-color);
}

[data-theme="watermelon"] .widget.shortcut-widget-box .widget-head .widget-title:hover span {
  color: var(--main-color);
}

[data-theme="watermelon"] .widget-body .link-item:hover {
  color: #fff;
  background: var(--third-color);
}

[data-theme=watermelon] .widget.spacer {
    box-shadow: none;
    border: none;
}
[data-page-route=Workspaces] .widget.number-widget-box {
    border: 1px solid var(--second-color);
}
/* Dropdowns */
[data-theme="watermelon"] .dropdown-menu>li>a {
  font-size: 0.9rem;
  color: #666;
  transition: color 0.3s, background-color 0.3s;
}

[data-theme="watermelon"] .dropdown-menu>li:hover {
  background: linear-gradient(to left, var(--main-color), var(--second-color));
  color: #fff;
}

[data-theme="watermelon"] .awesomplete > ul > li[aria-selected=true] {
  background-color: transparent;
}

[data-theme="watermelon"] .awesomplete > ul > li:hover {
  background: linear-gradient(to left, var(--main-color), var(--second-color));
}

[data-theme="watermelon"] .awesomplete > ul > li:hover a {
  color: #fff;
}

/* Page Heading */
[data-theme="watermelon"] .page-head {
  border-bottom: none !important;
  box-shadow: 0 1px 10px 1px rgba(115, 108, 203, 0.1);
  border-radius: inherit;
  background-color: var(--second-color);
}

[data-theme="watermelon"] .page-title .title-text {
  color: #ffffff;
  font-size: 22px;
}

[data-theme="watermelon"] .widget .widget-head .widget-title,
[data-theme="watermelon"] .section-head,
[data-theme="watermelon"] .comment-input-header {
  font-size: 14px;
  color: var(--text-color);
}

/* Main Section */
[data-theme="watermelon"] .list-row-head {
  background-color: var(--second-color);
  border-bottom: 1px solid #e4e4e4 !important;
}

/* Buttons */
[data-theme="watermelon"] .btn-primary {
  background-color: var(--second-color);
  border-color: var(--main-color);
  color: #fff;
  padding: 12px 25px;
  font-size: 1rem;
  border-radius: var(--border-radius);
  transition: background-color 0.3s, transform 0.3s;
}

[data-theme="watermelon"] .btn-primary:hover {
  background-color: var(--second-color);
  transform: scale(1.05);
}

[data-theme="watermelon"] .btn-default:hover,
[data-theme="watermelon"] .btn-default:focus, .btn-default.focus,
[data-theme="watermelon"] .btn-default:active, .btn-default.active,
[data-theme="watermelon"] .open > .dropdown-toggle.btn-default {
  background-color: var(--third-color) !important;
  color: #fff !important;
}

[data-theme="watermelon"] .btn-default:hover svg {
  stroke: #fff !important;
  fill: #fff !important;
  color: #fff !important;
}

[data-theme="watermelon"] .btn-secondary,
[data-theme="watermelon"] .btn-secondary:focus, .btn-secondary.focus,
[data-theme="watermelon"] .btn-secondary:active, .btn-secondary.active,
[data-theme="watermelon"] .open > .dropdown-toggle.btn-secondary {
  background-color: var(--third-color);
  color: #fff;
}

[data-theme="watermelon"] .btn-secondary:hover {
  background-color: var(--main-color);
  color: #fff;
}

[data-theme="watermelon"] .btn-primary:hover,
[data-theme="watermelon"] .btn-primary:focus, .btn-primary.focus,
[data-theme="watermelon"] .btn-primary:active, .btn-primary.active,
[data-theme="watermelon"] .open > .dropdown-toggle.btn-primary {
  background-color: var(--main-color) !important;
  border-color: var(--main-color) !important;
}

/* Other Components */
[data-theme="watermelon"] input[type=checkbox]:checked:before {
  color: var(--third-color);
}

[data-theme="watermelon"] input[type=checkbox]{
  background-color: #ffffff;
}
[data-theme=watermelon] input[type=checkbox]:checked{
    background: var(--third-color);
}
[data-theme=watermelon] .frappe-control .ql-editor:not(.read-mode){
    background: var(--widget-bg);
}
[data-theme=watermelon] .input-with-feedback{
  background-color: #f3f3f3;
}
[data-theme=watermelon] .like-disabled-input{
    background-color: #e8eafcad;
}
[data-theme=watermelon] .document-link-badge span{
    background-color: var(--main-color) !important;
    color: #fff;
}

[data-theme=watermelon] .document-link-badge{
    background-color: var(--widget-bg)!important;
}
[data-theme=watermelon] .document-link button{
    background: var(--third-color) !important;
    color: #fff !important;
}

[data-theme=watermelon] .document-link button svg{
    fill: white;
    stroke: white;
}
[data-theme=watermelon] .indicator-pill.gray,
[data-theme=watermelon] .indicator-pill-right.gray,
[data-theme=watermelon] .indicator-pill-round.gray {
    background: var(--third-color);
    color: #fff;
}
[data-theme=watermelon] .indicator-pill.green,
[data-theme=watermelon] .indicator-pill-right.green,
[data-theme=watermelon] .indicator-pill-round.green {
    color: var(--main-color);
}

[data-theme=watermelon] .notifications-icon svg{
    stroke: #fff;
    fill: #fff;
}

[data-theme=watermelon] #page-Workspaces .page-head{
    background-color: var(--second-color);
    border-radius: inherit;
}

.page-form {
    margin: 0;
    padding: var(--padding-xs);
    display: flex;
    flex-wrap: wrap;
    background-color: #161512;
    border-bottom: 1px solid var(--border-color);
    border-radius: inherit;
}

/* Prevent hover animation for the specific element */
[data-theme="watermelon"] .widget:not(.ce-paragraph) {
  transition: transform 0.3s ease-in-out;
}

[data-theme="watermelon"] .widget.ce-paragraph:hover {
  transform: none; /* Prevent animation */
}

[data-theme="watermelon"] .checkbox .label-area {
    font-size: var(--text-sm);
    font-weight: var(--weight-medium);
    letter-spacing: 0.02em;
    color: #ffffff;
}

[data-theme="watermelon"] span.indicator-pill.green.filterable.no-indicator-dot.ellipsis {
    background-color: darkgray;
}

[data-theme=watermelon] .indicator-pill.green, [data-theme=watermelon] .indicator-pill-right.green, [data-theme=watermelon] .indicator-pill-round.green {
    color: #ffffff;
}

.control-data[data-v-44154705] {
    height: calc(100vh - 202px);
    overflow-y: auto;
    padding: 8px;
}

[data-theme=watermelon] .std-form-layout > .form-layout > .form-page {
    border-radius: var(--border-radius-md);
    border: 1px solid var(--border-color);
    box-shadow: none;
    background-color: var(--main-color);
}
[data-theme=watermelon] .control-label, .grid-heading-row {
    color: white;
    font-size: var(--text-sm);
    font-weight: var(--weight-regular);
    letter-spacing: 0.02em;
}
[data-theme=watermelon] label.control-label.reqd {
    color: white;
    font-weight: bold;
}
[data-theme=watermelon] * {
    scrollbar-width: thin;
    scrollbar-color: #000000 #EDE9E4;
}
[data-theme=watermelon] [route=Workspaces] .layout-main .layout-main-section-wrapper {
        height: 100%;
        overflow-y: auto;
        scrollbar-color: #000000 transparent;
}

[data-theme=watermelon] .page-form.flex {
    background-color: var(--second-color);
}

[data-theme=watermelon] span.level-item {
    color: white;
}

[data-theme=watermelon] .list-row-col.ellipsis.hidden-xs {
    color: white;
}

[data-theme=watermelon] p.help-box.small.text-muted {
    color: white;
}

[data-theme=watermelon] h1, [data-theme=watermelon] h2, [data-theme=watermelon] h3, [data-theme=watermelon] h4, [data-theme=watermelon] h5, [data-theme=watermelon] h6 {
    color: white;
    font-weight: bold;
    letter-spacing: 0.5px;
}

[data-theme=watermelon] .modal .modal-content {
    background-color: var(--second-color);
    border-color: var(--border-color);
}

[data-theme=watermelon] .grid-heading-row {
    background-color: black;
}

[data-theme=watermelon] .small.form-clickable-section.grid-footer {
    background-color: transparent;
}

[data-theme=watermelon] .new-timeline .timeline-item {
    color: var(--second-color);
}

[data-theme=watermelon] .new-timeline .activity-title, .new-timeline .timeline-actions {
    background-color: transparent;
}

/* Make all icons white */
[data-theme="watermelon"] svg {
    fill: #3c93f7 !important;
    color: #76a8e3 !important;
    stroke: #76a8e3 !important;
}

[data-theme="watermelon"] svg.frappe-chart.chart {
    border-radius: 5px;
    fill: #818181 !important;
    stroke: #818181 !important;
    color: #818181 !important;
}

[data-theme=watermelon] .notifications-icon svg {
    stroke: #a9c0f1;
    fill: #a9c0f1;
    width: 26px;
    height: 18px;
}

[data-theme=watermelon] p.help-box.small.text-muted {
    color: var(--text-muted) !important;
}

[data-theme=watermelon] code {
    color: #00b2ff;
}

[data-theme=watermelon] .timestamp.text-muted {
    color: grey !important;
}


[data-theme=watermelon] .list-no-data-state.text-muted {
    color: grey !important;
}

[data-theme=watermelon] .widget.dashboard-widget-box .widget-subtitle {
    color: var(--second-color);
}

[data-theme=watermelon] span.modified {
    color: var(--second-color);
}

[data-theme=watermelon] span.comment-count.d-flex.align-items-center {
    color: var(--second-color);
}

[data-theme=watermelon] .properties {
    background-color: var(--main-color);
}
